<template>
  <div>
    <h1>{{id ? '编辑' : '新建' }}广告</h1>
    <el-form>
      <el-form-item label="备注" label-width="120px">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="广告" label-width="120px">
        <el-button type="text" @click="model.items.push({})">
          <i class="el-icon-plus">添加广告项</i>
        </el-button>
        <el-row type="flex" style="flex-wrap:wrap">
          <el-col :md="24" v-for="(item, i) in model.items" :key="i">
            <el-card style="margin:10px">
              <el-form-item label="跳转链接(url)" label-width="120px">
                <el-input v-model="item.url"></el-input>
              </el-form-item>
              <el-form-item label="图片" label-width="120px" style="margin-top:20px">
                <el-upload
                  class="avatar-uploader"
                  :action="$http.defaults.baseURL + '/upload'"
                  :show-file-list="false"
                  :on-success="res => $set(item, 'image', res.url)"
                >
                  <img v-if="item.image" :src="item.image" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label-width="120px">
                <el-button type="danger" @click="model.items.splice(i, 1)">删除</el-button>
              </el-form-item>
            </el-card>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="save">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: String
    }
  },
  data() {
    return {
      model: {
        items: []
      }
    };
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`/rest/ads/${this.id}`, this.model);
      } else {
        await this.$http.post("/rest/ads", this.model);
      }
      this.$router.push("/ads/list");
      this.$message({
        type: "success",
        message: "保存成功"
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/ads/${this.id}`);
      // this.model = res.data;
      this.model = Object.assign({}, this.model, res.data);
    }
  },
  created() {
    this.id && this.fetch();
  }
};
</script>

